import { isBrowser } from "browser-or-node";
    export default {
        data() {
            return {}
        },
        computed: {
            apexchart() {
                if (isBrowser) {
                    return () =>
                    import('vue-apexcharts');
                } else {
                    return 'hjhjhj'
                }                
            },
            options() {
                return {
                    chart: {
                        id: 'donut',
                    },
                    legend: {
                        show: true
                    },
                    labels:['1991', 1992]
                }
            },
            series() {
                return [44, 55]
            }
        }
    }
